import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { IPageId, PAGE_ID } from '../../../configs/page-id.config';
import { AppService } from '../../../shareds/services/app.service';
import { BaseListComponent } from '../../../base-list.component';
import { Tenant } from './tenant.model';
import { ToastrService } from 'ngx-toastr';
import { TenantService } from './tenant.service';
import { ISearchResult } from '../../../interfaces/isearch.result';
import { TenantFormComponent } from './tenant-form.component';
import { finalize, map } from 'rxjs/operators';
import { DestroySubscribers } from '../../../shareds/decorator/destroy-subscribes.decorator';
import { CheckPermission } from '../../../shareds/decorator/check-permission.decorator';

@Component({
    selector: 'app-tenant',
    templateUrl: './tenant.component.html'
})

@DestroySubscribers()
@CheckPermission()
export class TenantComponent extends BaseListComponent<Tenant> implements OnInit {
    @ViewChild(TenantFormComponent) tenantFormComponent: TenantFormComponent;
    isActive: boolean;

    constructor(@Inject(PAGE_ID) private pageId: IPageId,
                private toastr: ToastrService,
                private appService: AppService,
                private tenantService: TenantService) {
        super();
    }

    ngOnInit() {
        this.appService.setupPage(this.pageId.CONFIG, this.pageId.CONFIG_TENANT, 'Qun l Tenant', 'Danh sch tenant');
        this.search(1);
    }

    search(currentPage: number) {
        this.currentPage = currentPage;
        this.isSearching = true;
        this.listItems$ = this.tenantService.search(this.keyword, this.isActive, this.currentPage, this.pageSize)
            .pipe(finalize(() => this.isSearching = false),
                map((result: ISearchResult<Tenant>) => {
                    this.totalRows = result.totalRows;
                    return result.items;
                })
            );
    }

    add() {
        this.tenantFormComponent.add();
    }

    edit(tenant: Tenant) {
        this.tenantFormComponent.edit(tenant);
    }
}
