import { PageTranslation } from './page-translation.model';

export class Page {
    id: number;
    pageId: number;
    name: string;
    clientId: string;
    isActive: boolean;
    url: string;
    icon: string;
    bgColor: string;
    order: number;
    parentId?: number;
    childCount: number;
    idPath: string;
    namePrefix: string;
    pageTranslation: PageTranslation[];

    constructor(id?: number, name?: string, clientId?: string, isActive?: boolean, url?: string, icon?: string, bgColor?: string,
                idPath?: string, order?: number, parentId?: number, childCount?: number, pageTranslation?: PageTranslation[]) {
        this.id = id;
        this.name = name ? name : '';
        this.clientId = clientId ? clientId : '';
        this.isActive = true;
        this.url = url ? url : '';
        this.icon = icon ? icon : '';
        this.bgColor = bgColor;
        this.order = 0;
        this.parentId = parentId;
        this.childCount = childCount ? childCount : 0;
        this.idPath = '';
        this.namePrefix = '';
        this.pageTranslation = pageTranslation ? pageTranslation : [];
    }
}
