import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PageComponent } from './page/page.component';
import { ClientComponent } from './client/client.component';
import { ClientFormComponent } from './client/client-form.component';
import { ClientService } from './client/client.service';
import { AuthGuardService } from '../../shareds/services/auth-guard.service';
import { TenantComponent } from './tenant/tenant.component';
import { TenantService } from './tenant/tenant.service';
import { LayoutComponent } from '../../shareds/layouts/layout.component';
import { RoleComponent } from './role/role.component';
import { LanguageComponent } from './language/language.component';
import { PageService } from './page/page.service';
import { RoleService } from './role/role.service';
import { UserSettingComponent } from './user-setting/user-setting.component';

const routes: Routes = [
    // {
    //     path: '',
    //     component: LayoutComponent,
    //     canActivate: [AuthGuardService],
    //     children: [
    {
        path: '',
        component: RoleComponent
    },
    {
        path: 'tenants',
        component: TenantComponent,
    },
    {
        path: 'pages',
        component: PageComponent,
        resolve: {
            data: PageService
        }
    },
    {
        path: 'clients',
        resolve: {
            data: ClientService
        },
        component: ClientComponent,
        children: [
            {
                path: 'add-new',
                component: ClientFormComponent
            },
            {
                path: 'edit',
                component: ClientFormComponent
            }
        ]
    },
    {
        path: 'roles',
        resolve: {
            data: RoleService
        },
        component: RoleComponent
    },
    {
        path: 'languages',
        component: LanguageComponent
    },
    {
        path: 'settings',
        component: UserSettingComponent
    }
    // ]
    // },
    // {
    //     path: '**',
    //     redirectTo: '404',
    //     pathMatch: 'full'
    // }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
    providers: [ClientService, TenantService, PageService, RoleService]
})
export class ConfigRoutingModule {
}
