import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { BaseListComponent } from '../../../base-list.component';
import { Role } from './models/role.model';
import { AppService } from '../../../shareds/services/app.service';
import { IPageId, PAGE_ID } from '../../../configs/page-id.config';
import { RoleService } from './role.service';
import { ISearchResult } from '../../../interfaces/isearch.result';
import { SpinnerService } from '../../../core/spinner/spinner.service';
import { ToastrService } from 'ngx-toastr';
import { RoleFormComponent } from './role-form/role-form.component';
import { finalize, map } from 'rxjs/operators';
import { CheckPermission } from '../../../shareds/decorator/check-permission.decorator';
import { ActivatedRoute } from '@angular/router';
import { RoleDetailComponent } from './role-detail/role-detail.component';

@Component({
    selector: 'app-role',
    templateUrl: './role.component.html',
    providers: [RoleService]
})
@CheckPermission()
export class RoleComponent extends BaseListComponent<Role> implements OnInit {
    @ViewChild(RoleFormComponent) roleFormComponent: RoleFormComponent;
    @ViewChild(RoleDetailComponent) roleDetailComponent: RoleDetailComponent;

    constructor(@Inject(PAGE_ID) private pageId: IPageId,
                private route: ActivatedRoute,
                private appService: AppService,
                private spinnerService: SpinnerService,
                private toastr: ToastrService,
                private roleService: RoleService) {
        super();
    }

    ngOnInit() {
        this.appService.setupPage(this.pageId.CONFIG, this.pageId.CONFIG_ROLE, 'Qun l quyn', 'Danh sch quyn');
        this.listItems$ = this.route.data.pipe(map((result: { data: ISearchResult<Role> }) => {
            const data = result.data;
            this.totalRows = data.totalRows;
            return data.items;
        }));
    }

    search(currentPage: number) {
        this.currentPage = currentPage;
        this.isSearching = true;
        this.listItems$ = this.roleService.search(this.keyword, this.currentPage)
            .pipe(finalize(() => this.isSearching = false),
                map((result: ISearchResult<Role>) => {
                    this.totalRows = result.totalRows;
                    return result.items;
                }));
    }

    delete(id: string) {
        this.spinnerService.show('ang xa quyn. Vui lng i...');
        this.roleService.delete(id)
            .subscribe(() => {
                this.search(this.currentPage);
            });
    }

    add() {
        this.roleFormComponent.add();
    }

    edit(role: Role) {
        this.roleFormComponent.edit(role);
    }

    detail(role: Role) {
        this.roleDetailComponent.show(role);
    }
}
